import IntegrationIcons from '@/assets/integration_icons';
import { IExposedProperty } from '@/components';
import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ir-footer',
  styleUrl: 'ir-footer.css',
  shadow: true,
})
export class IrFooter {
  @Prop() exposedProperty: IExposedProperty;
  render() {
    if (!this.exposedProperty) {
      return null;
    }
    return (
      <footer class="py-2 border-t flex items-center px-4 lg:px-6">
        <ul class="flex items-center flex-col space-y-2  justify-between w-full md:space-y-0 md:flex-row">
          <li class="flex justify-between md:flex-col w-full md:w-fit">
            <p class="font-medium">{this.exposedProperty.name}</p>
            <div class="flex items-center gap-2 md:gap-4 text-sm">
              <a href={`tel:${this.exposedProperty.phone}`}>{this.exposedProperty.phone}</a>
              <ir-privacy-policy></ir-privacy-policy>
            </div>
          </li>
          <li class="flex items-center gap-4">
            {this.exposedProperty.social_media.map(media => {
              if (media.link === '') {
                return null;
              }
              const href = media.code === '006' ? `https://api.whatsapp.com/send/?phone=${media.link}` : media.link;
              return (
                <a target="_blank" href={href} title={media?.name}>
                  {IntegrationIcons[media.code]}
                </a>
              );
            })}
          </li>
          <li class="">
            <a href="https://info.igloorooms.com/" target="_blank" title="igloorooms cloud booking solutions for hotels">
              <img src={'/assets/BE_logo_footer.png'} alt="igloorooms web booking engine" class="size-10 w-32 object-contain" />
            </a>
          </li>
        </ul>
      </footer>
    );
  }
}
